export default {
	data() {
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			info: [],
			shop: []
		};
	},
	onLoad() {
		var _this = this;
		// var url = _this.Global.url
		// console.log(url)
		
		uni.request({
			url: 'https://exam.qhynice.top/index.php/Api/Index/index',
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded'
			},
			success: res => {
				console.log(res);
				_this.info = res.data.ggtop;
				_this.shop = res.data.prolist;
			}
		});
		// uni.navigateTo({
		// 	url: '../bindleader/bindleader'
		// });
	},
	methods: {
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots;
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay;
		},
		// 进入商品详情页
		detail: function(id) {
			var uid = uni.getStorageSync('id')
			console.log(uid)
			if (!uni.getStorageSync('id')) {
				uni.showModal({
					title: '温馨提示',
					content: '请进行授权',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url:'../login/login'
							})
						} else if (res.cancel) {
							uni.switchTab({
								url:'../index/index'
							})
						}
					}
				});
			}else{
				uni.setStorageSync('ids', id);
				uni.navigateTo({
					url: '../details/details'
				});
			}
		},
		// 解密手机号（通过想后台获取的session_key解密手机号）
		getPhoneNumber: function(e) {
			console.log(e);
			console.log(e.detail.errMsg == 'getPhoneNumber:ok');
			if (e.detail.errMsg == 'getPhoneNumber:ok') {
				uni.request({
					url: 'https://exam.qhynice.top/index.php/Api/User/deciphering',
					method: 'GET',
					data: {
						sessionID: uni.getStorageSync('session_key'),
						encryptedDataStr: e.detail.encryptedData,
						iv: e.detail.iv
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: res => {
						console.log(res.data.phoneNumber);
					}
				});
			} else {
				uni.showToast({
					title: '获取手机号失败',
					duration: 2000,
					icon: 'none'
				});
			}
		}
	}
};
